body {
  margin: 0;
  font-family: sans-serif;
  background-color: #f2f2f2; /* Light gray background */
}

table {
  width: 100%;
  max-width: 800px;
  border-collapse: collapse;
  margin: 20px auto;
  background: white;
}

th, td {
  padding: 10px;
  text-align: center;
  border: 1px solid #ddd;
}

h1, h2 {
  text-align: center;
}
